.footer {
  width: 100%;
  height: 10vh;
  background: var(--navbar-footer-color);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 5px;
}

.footer p {
  color: var(--links-main-color);
  margin: 15px;
  font-size: 20px;
}

@media only screen and (max-width: 600px) {
  .footer {
    font-size: 30px;
  }
}
