.pageNotFound {
  width: 100%;
  height: 80vh;
  font-family: "Arial", sans-serif;
  background-color: #21325e;
  color: #ffffff;
  display: flex;
  align-items: center;
}

.pageNotFound h2 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 80px;
  color: #e9d35b;
  height: auto;
}

@media only screen and (max-width: 600px) {
  .pageNotFound h2 {
    font-size: 40px;
  }
}
