.project {
  width: 80%;
  min-height: 80vh;
  margin: auto;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: fadeIn ease 2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.project h1 {
  font-size: 40px;
}

.project img {
  width: 40%;
  border-radius: 10px;
}

.project p {
  width: 40%;
  font-size: 1.5rem;
  color: var(--text-side-color);
  word-wrap: break-word;
}

.project a {
  padding: 20px 0;
}

.project svg {
  font-size: 60px;
  color: var(--text-side-color);
}

.project svg:hover {
  animation: fadeIn ease 2s;
}

@media only screen and (max-width: 800px) {
  .project img {
    width: 100%;
  }

  .project p {
    width: 100%;
    font-size: 1.25rem;
  }
}
