.home {
  width: 100%;
  height: 80vh;
  align-items: center;
  font-family: "Arial", sans-serif;
  animation: landing ease 4s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.about {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: var(--main-section-color);
  color: var(--text-side-color);
}

.about h2 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 80px;
  color: #e9d35b;
  height: 50px;
}

.about .prompt {
  width: 40%;
  font-size: 30px;
}
.prompt svg {
  font-size: 60px;
  margin: 5px;
  color: var(--text-side-color);
}

.prompt svg:hover {
  animation: fadeIn ease 2s;
}

.prompt a {
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .about h2 {
    font-size: 40px;
  }

  .about .prompt {
    margin-top: 10px;
    font-size: 20px;
    width: 80%;
  }
}
