
:root {
  --text-side-color: #222222;
  --text-main-color: #fdc500;
  --links-main-color: white;
  --links-side-color: #fdc500;
  --main-section-color: white;
  --navbar-footer-color: #00296b;
}
.App {
  text-align: center;
}

body {
  padding: 0;
  margin: 0;
}

@keyframes landing {
  0% { transform: rotateY(20deg); }
  25% { transform: rotateX(20deg); }
  50% { transform: rotateY(20deg); }
  75% { transform: rotateX(20deg); }
  100% { transform: rotate(0); }
}

@keyframes fadeIn {
  0% { opacity: 0.4; }
  100% { opacity: 1; }
}

@keyframes scale {
  0% { transform: scale(1.2);}
  100% { transform: scale(1);}
}